	<template>
 <view class="container">
 	<view class="userinfo">
 		<view class="userinfo-inner">
 			<open-data class="userinfo-avatar" type="userAvatarUrl"></open-data>
 			<view class="iser-info-box">
 				<view class="userinfo-nickname" :wx:if="userInfoStatus == 2">
 					<open-data class="userinfo-nickname" type="userNickName"></open-data>
 				</view>
 				<view class="login-btn"  @click="goLogin()">微信一键登录</view>
				
 				<text :wx:if="score_sign_continuous == 0" class="userinfo-score-day" bindtap="score">今日未签到</text>
 				<text :wx:if="score_sign_continuous > 0" class="userinfo-score-day" bindtap="score">已连续签到{{score_sign_continuous}}天</text>
 			</view>
 		</view>
 		<image src="/static/user-bg.jpg"></image>
 	</view>
 	<view class="userinfo-notice" :wx:if="noticeList.length">
 		<image  />
 		<swiper :wx:if="noticeList" class="notice_swiper" vertical="true" autoplay="true" circular="true" interval="6000">
 			<navigator :wx:for-items="noticeList.dataList" wx:key="id" :url="'/pages/notice/notice?id='+item.id" hover-class="none"
 			 open-type="navigate">
 				<swiper-item>
 					<view class="notice_itemr">{{item.title}}</view>
 				</swiper-item>
 			</navigator>
 		</swiper>
 	</view>
 	<view class="userinfo-item">
 		<image src="/static/profile_dfunding.png"></image>
 		<navigator url="/pages/partner/partner" hover-class="none">活动<image src="/static/arrow-right.png"></image>
 		</navigator>
 	</view>
 	<view class="cu-list menu shadow bg-white sm-border" style="width: 690rpx;">
 		<view class="cu-item arrow">
 			<view class="content"  hover-class="none" @click="jumpTochange()">
 				<text class="cuIcon-card text-orange"></text>
 				<text class="text-grey">积分商城</text>
 			</view>
 		</view>
 		
 		<view class="cu-item arrow" @click="address">
 			<navigator class="content" url="/pages/address/address" hover-class="none">
 				<text class="cuIcon-usefull text-orange"></text>
 				<text class="text-grey">收货地址</text>
 			</navigator>
 		</view>
 		<view class="cu-item arrow">
 			<navigator class="content" url="/pages/order/order" hover-class="none">
 				<text class="cuIcon-light text-orange"></text>
 				<text class="text-grey">我的订单</text>
 			</navigator>
 		</view>
	
 		<view class="cu-item arrow" @click="makeCall">
 			<navigator class="content" url="/pages/coupons/index" hover-class="none">
 				<text class="cuIcon-service text-orange"></text>
 				<text class="text-grey">回收员招募</text>
 			</navigator>
 		</view>
 		<view class="cu-item arrow"  @click="makeCall">
 			<navigator class="content" url="/pages/coupons/index" hover-class="none">
 				<text class="cuIcon-we text-orange"></text>
 				<text class="text-grey">客服咨询</text>
 			</navigator>
 		</view>
 		<view class="cu-item arrow">
 			<navigator class="content" url="/pages/coupons/index" hover-class="none">
 				<text class="cuIcon-similar text-orange"></text>
 				<text class="text-grey">设置</text>
 			</navigator>
 		</view>
 	</view>
 	<view class="padding text-center text-sm text-gray">
 		小0回收
 	</view>
 </view>


</template>

<script>
  export default {
    data() {
      
      return {
        	balance: 0,
        		freeze: 0,
        		score: 0,
        		score_sign_continuous: 0,
        		tabClass: ["", "", "", "", ""],
        		userInfo: null,
        		token: null,
        		version: null,
        		noticeList: [],
        		loaded: false,//避免首次打开用户中心重复获取数据,
        		// 判断有没有用户详细资料
        		userInfoStatus: 0 // 0 未读取 1 没有详细信息 2 有详细信息
      };
    },
	methods:{
		scan:function(){
			uni.scanCode({
				success() {
					
				}
			})
		},
		makeCall:function(){
			uni.makePhoneCall({
				phoneNumber:'123456789'
			})
		},
		
		goLogin(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
    jumpTochange(){
      uni.switchTab({
        url:'/pages/change/change'
      })
    }
		
	},
	
   
    
    	
   
  }
</script>

