.container {
  height: 100%;
  min-height: 100%;
  overflow: hidden;
  overflow-y: hidden;
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}
.userinfo {
  background: #fff;
  width: 100%;
  height: 400rpx;
  text-align: center;
  position: relative;
}
.userinfo-inner{
	position: relative;
	z-index: 1;
}
.userinfo image{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
}

.userinfo button {
  position: absolute;
  width: 100%;
  height: 300rpx;
  top: 0;
  opacity: 0;
}

.userinfo-avatar {
  border-radius: 50%;
  margin-top: 130rpx;
  display: inline-block;
  overflow: hidden;
  width: 150rpx;
  height: 150rpx;
}

.iser-info-box {
  height: 100rpx;
	text-align: center;
	display: block;
}

.userinfo-nickname {
  color: #fff;
  font-size: 32rpx;
	text-align: center;
	margin-bottom: 10rpx;
	display: block;
}

.userinfo-score {
  font-size: 24rpx;
  margin-top: 5px;
  background-color: #b3a078;
  color: #fff;
  padding: 1px 5px;
  border-radius: 2px;
  position: relative;
  z-index: 1;
}

.userinfo-score-day {
  font-size: 24rpx;
  margin-top: 5px;
  color: #fff;
}

.userinfo-item {
  width: 100%;
  border-bottom: 1px solid #fbfbfb;
  line-height: 100rpx;
  padding-left: 30px;
  background-color: #fff;
  font-size: 30rpx;
  color: #333;
}

.userinfo-item>image {
  width: 20px;
  height: 20px;
  margin: 0 5px -4px 0;
}

.userinfo-item navigator {
  display: inline-block;
  width: 90%;
  font-size: 30rpx;
}
.userinfo-item navigator image{
	height: 36rpx;
	width: 20rpx;
	float: right;
	margin: 32rpx 30rpx 0 0;
}


.userinfo-item navigator text {
  font-size: 24rpx;
  color: #999;
}

.userinfo-item-box {
  width: 100%;
}

.userinfo-item-box .userinfo-item {
  display: inline-block;
  width: 33.1%;
  text-align: center;
  height: 200rpx;
  padding-left: 0;
  line-height: 40rpx;
  position: relative;
}

.userinfo-item-box .userinfo-item:nth-child(1) {
  border-right: 1px solid #fbfbfb;
}

.userinfo-item-box .userinfo-item:nth-child(2) {
  border-right: 1px solid #fbfbfb;
}

.userinfo-item-box .userinfo-item:nth-child(4) {
  border-right: 1px solid #fbfbfb;
}

.userinfo-item-box .userinfo-item:nth-child(5) {
  border-right: 1px solid #fbfbfb;
}

.userinfo-item-box .userinfo-item image {
  width: 50rpx;
  height: 50rpx;
  display: block;
  margin: 0 auto;
  margin-top: 40rpx;
  margin-bottom: 12rpx;
}

.userinfo-item-box .userinfo-item text {
  position: absolute;
  bottom: 20rpx;
  width: 100%;
  left: 0;
  color: #999;
  font-size: 22rpx;
}

.userinfo-item-box .userinfo-item navigator {
  font-size: 26rpx;
  color: #333;
  margin-top: 5rpx;
